import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { ResultItem } from '..';
import { getAllActivities, getAllHotels, getAllRestaurant, getAllScenicSpots } from '../../api';
import { PAGE_NUM } from '../../constants/pageData';
import {
  Container,
  Frame,
  DetailCont,
  Line,
  Title1,
  Title2,
  Paragraph,
  Img,
  RecommendCont,
  RecommendHeader,
  TravelCont,
  ItemsBlock,
} from './styles';

const Detail = () => {
  const [scenicSpotsData, setScenicSpotsData] = useState([]);
  const [hotelsData, setHotelsData] = useState([]);
  const [activitiesData, setActivitieData] = useState([]);
  const [restaurantData, setRestaurantData] = useState([]);
  const location = useLocation();
  const {
    Name,
    Address,
    Phone,
    OpenTime,
    TicketInfo,
    DescriptionDetail,
    Picture,
    TravelInfo,
    Position,
    StartTime,
    EndTime
  } = location.state;

  const parseIsoDatetime = dtstr => {
    var dt = dtstr.split(/[: T-]/).map(parseFloat);
    return `${dt[0]}/${dt[1]}/${dt[2]}`;
  };

  let openTime = OpenTime === 'Sun 24 hours；Mon 24 hours；Tue 24 hours；Wed 24 hours；Thu 24 hours；Fri 24 hours；Sat 24 hours' ?
    '全天候開放' : (OpenTime ?? '');
  let duration = (StartTime && EndTime) ? `${parseIsoDatetime(StartTime)} ~ ${parseIsoDatetime(EndTime)}` : '';
  let Infos = [
    { text: Address },
    { text: Phone },
    { text: openTime || duration },
    { text: TicketInfo },
    { text: DescriptionDetail },
  ];

  useEffect(() => {
    window.scroll(0, 0);

    let searchParam = new URLSearchParams([
      ['$top', PAGE_NUM],
      ['$filter', 'Picture/PictureUrl1 ne null'],
      ['$format', 'JSON'],
      ['$spatialFilter', `nearby(${Position.PositionLat}, ${Position.PositionLon}, 1000)`]
    ]);

    getNearByData(getAllScenicSpots, searchParam, setScenicSpotsData, 'scenicSpots');
    getNearByData(getAllHotels, searchParam, setHotelsData, 'hotels');
    getNearByData(getAllActivities, searchParam, setActivitieData, 'activities');
    getNearByData(getAllRestaurant, searchParam, setRestaurantData, 'restaurant');
  }, []);

  const getNearByData = async (getData, searchParam, setData, type) => {
    try {
      let resp = await getData(searchParam.toString());
      //如在該分類之詳細頁，將不顯示自身資料
      location.pathname.includes(type) ? setData(resp.data.slice(1)) : setData(resp.data);
    } catch (error) {
      console.log('nearBy data get error', error);
    }
  };

  return (
    <Container>
      <Frame>
        <DetailCont>
          <Title1>{Name}</Title1>
          {
            Infos.map((item, index) => (
              <Line key={index}>
                <Paragraph>{item.text}</Paragraph>
              </Line>
            ))
          }
        </DetailCont>
        <Img src={Picture.PictureUrl1} alt={Picture.PictureDescription1} />
      </Frame>
      {TravelInfo &&
        <TravelCont>
          <Title2>交通方式</Title2>
          <Paragraph>{TravelInfo}</Paragraph>
        </TravelCont>
      }
      {
        scenicSpotsData.length > 0 &&
        <RecommendCont>
          <RecommendHeader>看看附近的景點</RecommendHeader>
          <ItemsBlock>
            {
              scenicSpotsData.map((item, index) => (
                <ResultItem
                  key={index}
                  data={item}
                  margin={'0 40px 0 0'}
                />
              ))
            }
          </ItemsBlock>
        </RecommendCont>
      }
      {
        restaurantData.length > 0 &&
        <RecommendCont>
          <RecommendHeader>看看附近的餐飲</RecommendHeader>
          <ItemsBlock>
            {
              restaurantData.map((item, index) => (
                <ResultItem
                  key={index}
                  data={item}
                  margin={'0 40px 0 0'}
                />
              ))
            }
          </ItemsBlock>
        </RecommendCont>
      }
      {
        hotelsData.length > 0 &&
        <RecommendCont>
          <RecommendHeader>看看附近的旅宿</RecommendHeader>
          <ItemsBlock>
            {
              hotelsData.map((item, index) => (
                <ResultItem
                  key={index}
                  data={item}
                  margin={'0 40px 0 0'}
                />
              ))
            }
          </ItemsBlock>
        </RecommendCont>
      }
      {
        activitiesData.length > 0 &&
        <RecommendCont>
          <RecommendHeader>看看附近的活動</RecommendHeader>
          <ItemsBlock>
            {
              activitiesData.map((item, index) => (
                <ResultItem
                  key={index}
                  data={item}
                  margin={'0 40px 0 0'}
                />
              ))
            }
          </ItemsBlock>
        </RecommendCont>
      }
    </Container>
  );
};

export default Detail;
