import React, { useEffect, useState } from 'react';
import {
  DetailCont,
  Image,
  Item,
  Name,
  Content1,
  Line,
  Icon,
  CardTag,
  ClassText,
} from './style';

import AddressSmall from '../../images/景介紹頁-locol icon.png';
import AddressMedium from '../../images/景介紹頁-locol icon@2x.png';

// import PhoneSmall from '../../images/景介紹頁-phone icon.png';
// import PhoneMedium from '../../images/景介紹頁-phone icon@2x.png';

// import CommentSmall from '../../images/景介紹頁-long comment icon.png';
// import CommentMedium from '../../images/景介紹頁-long comment icon@2x.png';

import BookSmall from '../../images/景介紹頁-book icon.png';
import BookMedium from '../../images/景介紹頁-book icon@2x.png';

// import MoneySmall from '../../images/宿介紹-money icon-1.png';
// import MoneyMedium from '../../images/宿介紹-money icon-1@2x.png';

import { useLocation } from 'react-router-dom';

const ResultItem = React.forwardRef((props, ref) => {
  const { data, margin } = props;
  const {
    Picture,
    Address,
    Class,
    Class1,
    Class2,
    Class3,
    StartTime,
    EndTime
  } = data;
  const location = useLocation();
  const [arrClass, setArrClass] = useState([]);

  // let description = data?.Description || data?.DescriptionDetail;
  // description = description?.length > 50 ? (description?.slice(0, 50) ?? '') + '...' : description;

  let openTime = data?.OpenTime === 'Sun 24 hours；Mon 24 hours；Tue 24 hours；Wed 24 hours；Thu 24 hours；Fri 24 hours；Sat 24 hours' ?
    '全天候開放' : (data?.OpenTime ?? '');

  useEffect(() => {
    let classes = []; //類別
    Class && classes.push(Class);
    Class1 && classes.push(Class1);
    Class2 && classes.push(Class2);
    Class3 && classes.push(Class3);
    setArrClass(classes);
  }, []);

  const parseIsoDatetime = dtstr => {
    var dt = dtstr.split(/[: T-]/).map(parseFloat);
    return `${dt[0]}/${dt[1]}/${dt[2]}`;
  };
  let duration = (StartTime && EndTime) ? `${parseIsoDatetime(StartTime)} ~ ${parseIsoDatetime(EndTime)}` : '';

  return (
    <Item
      ref={ref}
      key={data.ID}
      to={data.ID}
      state={data}
      margin={margin ?? ''}
    >
      <Image
        src={Picture?.PictureUrl1} alt={Picture?.PictureDescription1}
        isDetailPage={location.pathname.includes(location?.state?.ID)}
      />
      <DetailCont isDetailPage={location.pathname.includes(location?.state?.ID)}>
        <Name>{data?.Name?.replaceAll('.', '')}</Name>
        {
          Address &&
          <Line>
            <Icon src={AddressSmall} srcSet={`${AddressSmall} 1x, ${AddressMedium} 2x`} />
            <Content1 >{Address}</Content1>
          </Line>
        }
        {
          (openTime || duration) &&
          <Line>
            <Icon src={BookSmall} srcSet={`${BookSmall} 1x, ${BookMedium} 2x`} />
            <Content1>{openTime || duration}</Content1>
          </Line>
        }
        {/* {
          description &&
          <Line>
            <Icon src={CommentSmall} srcSet={`${CommentSmall} 1x, ${CommentMedium} 2x`} />
            <Content2 >{description}</Content2>
          </Line>
        } */}
        {
          arrClass.length > 0 &&
          <ClassText isDetailPage={location.pathname.includes(location?.state?.ID)}>
            <Icon src={BookSmall} srcSet={`${BookSmall} 1x, ${BookMedium} 2x`} />

            {
              arrClass.map((item, index) => (
                <CardTag key={index}>{item}</CardTag>
              ))
            }
          </ClassText>
        }
      </DetailCont>
    </Item>
  );
});
ResultItem.displayName = 'ResultItem';

export default ResultItem;
